body {
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.container-card {
  height: 40vh;
  width: 250px;
  display: flex;
  min-width: 250px;
  border-radius: 10px;
  align-items: center;
  background: #d7d7d7;
  flex-direction: column;
  box-sizing: border-box;
  box-shadow: 4px 5px 6px rgba(0, 0, 0, 0.4);
}

button {
  width: 180px;
  height: 40px;
  border: none;
  outline: none;
  color: white;
  cursor: pointer;
  font-size: 20px;
  font-weight: 600;
  margin-top: 30px;
  border-radius: 5px;
  background: #2f2c47;
  font-family: "Courier New", Courier, monospace;
}

button:active {
  transition: 0.2s;
  transform: scale(0.9);
}

p {
  font-size: 1.2em;
  font-weight: bolder;
  font-family: "Courier New", Courier, monospace;
}
